.main {
  width: 80%;
  margin: 10px auto;
}

.main-background-image {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  z-index: -1;
  background-image: url('./wood-background.jpg');
  background-repeat: repeat;
  background-size: contain;
  mask-image: linear-gradient(to bottom, rgba(0, 0, 0, 1), 75%, rgba(0, 0, 0, 0));
}

.page {
  padding: 20px;
}
